@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: roboto;
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

main {
  justify-content: center;
  display: flex;
}

a {
  color: #40779a;
  text-decoration: none;
}

#menu {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

li {
  list-style: none;
}

h1, h3, p {
  color: #f5f5f5;
}

#header-bg {
  opacity: .7;
  z-index: -1;
  background-color: #0000;
  background-image: linear-gradient(#14181c, #14181cf1 16.56%, #14181ccc 30.85%, #14181c9b 43.77%, #14181c64 56.23%, #14181c33 69.15%, #14181c0e 83.44%, #14181c00);
  background-repeat: repeat-x;
  height: 95px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

header {
  align-items: center;
  gap: 7em;
  width: 80%;
  margin: 0 auto;
  display: flex;
}

.label {
  color: #dae0e7;
}

#search-bar {
  width: 40rem;
  height: 2rem;
}

#submit {
  width: 10ch;
  height: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#nav-search-form {
  position: relative;
}

#live-search-dropdown {
  z-index: 100;
  background-color: #1f1f1f;
  width: 40rem;
  position: absolute;
}

.suggested-container {
  border-bottom: 1px solid #656565;
  align-items: center;
  height: 100px;
  display: flex;
}

.suggested-container:hover {
  cursor: pointer;
  background-color: #313131;
}

.suggested-poster-container {
  border-radius: 12px;
  width: 55px;
  height: 80%;
  margin: 0 15px 0 7px;
  overflow: hidden;
}

.suggested-poster {
  height: 100%;
}

.suggested-poster-fallback {
  width: 100%;
  height: 100%;
}

.suggested-info {
  color: #b5b5b5;
  font-size: 14px;
}

.suggested-year {
  margin-top: .3em;
  margin-bottom: .3em;
}

.movie-container {
  flex-direction: column;
  gap: 10px;
  width: 50%;
  margin: 1em 0;
  display: flex;
  position: relative;
}

#wrapper {
  width: 100vw;
  height: 100vh;
  position: relative;
}

#wrapper:before {
  content: "";
  background-image: var(--bg-url);
  filter: blur(8px);
  z-index: -1;
  background-position: center;
  background-size: cover;
  width: calc(100% + 40px);
  height: calc(100% + 40px);
  position: absolute;
  top: -20px;
  left: -20px;
}

.poster {
  height: 300px;
}

#bookmarks {
  background-color: #323232;
  width: 20rem;
  height: 95vh;
  padding: 10px;
  display: none;
}

.bookmark-button {
  cursor: pointer;
  background-color: #fc0;
  border: none;
  width: 10rem;
  padding: 10px;
  font-size: 16px;
}

.bookmark-button:hover {
  background-color: #e6b800;
}
